import { Component, OnInit } from '@angular/core';
import {RestService} from '../../../service/rest.service';
import {AssemblyMan, MeetingGroup, Period, TerritorialDivision} from '../../../interfaces/model';
import { ModalController } from '@ionic/angular';
import {ModalAsambleistaPage} from '../../modal/modal-asambleista/modal-asambleista.page';

@Component({
    selector: 'app-tab-ambito',
    templateUrl: './tab-ambito.page.html',
    styleUrls: ['./tab-ambito.page.scss'],
})
export class TabAmbitoPage implements OnInit {
    territorialList: TerritorialDivision[];
    territorialListFiltered: TerritorialDivision[];
    assemblyList: AssemblyMan[];
    photoMan: any;
    periodActive: Period;
    showListAssembly: boolean = false;
    territorialIdSelected: number = 0;

    constructor(private rest: RestService, public modalController: ModalController) { }

    ngOnInit() {
        this.getPeriod();
        this.getTerritorialList();
    }
    getPeriod() {
        this.rest.getPeriod().subscribe(respo => {
                this.periodActive =  respo;
            }, error1 => {
            console.log('Se ha producido un error');
            }
        );
    }
    getTerritorialList() {
        this.rest.getTerritorialList().subscribe( response => {
            this.territorialList = response;
            this.territorialListFiltered = this.territorialList;
        });
    }

    onChange($event) {
        if($event.target.value !== null){
            this.getAssemblymenTerritorialList($event.target.value);
        }
    }

    getAssemblymenTerritorialList(id: any){
        this.rest.getAssemblyMembersResource({
            periodId: this.periodActive.id,
            territorialId: id,
            politicalPartyId: 0,
            includePicture: false,
            onlyActives: true,
            onlyAlterns: false,
            assembly: 0,
            offset: 0,
            limit: 0
        }).subscribe( response => {
            this.assemblyList = response;
            this.getPhotosList();
        });
    }

    async changeModal(id, image) {
        const modal = await this.modalController.create({
            component: ModalAsambleistaPage,
            componentProps: {
                'assembly': this.assemblyList.filter(data => data.id == id)
            }
        });
        return await modal.present();
    }

    getPhotosList() {
        this.assemblyList.forEach((data) => {
            this.getPhoto(data);
        });
    }

    getPhoto(data: AssemblyMan) {
        this.rest.getAssemblymanPhoto({
            assemblyMemberId: data.id
        }).subscribe( responsePhoto => {
                const blob = new Blob([responsePhoto], {
                    type: 'image/png'
                });
                if (blob.size > 0) {
                    const reader = new FileReader();
                    reader.readAsDataURL(blob);
                    reader.onloadend = () => {
                        this.photoMan = reader.result;
                        data['imageChange'] = this.photoMan;
                    };
                } else {
                    data['imageChange'] = '../assets/images/silueta.png';
                }
            }, () => {
                data['imageChange'] = '../assets/images/silueta.png';
            }
        );
    }

    filterTerritorials(d: any) {
        const f = d.target.value.toString().trim().toLowerCase();
        if(f.length === 0){
            this.territorialListFiltered = this.territorialList;
        } else {
            this.territorialListFiltered = this.territorialList.filter(data => data.name.toLowerCase().includes(f))
        }
    }

    selectCommission(m: TerritorialDivision){
        this.getAssemblymenTerritorialList(m.id);
        this.territorialIdSelected = m.id;
        this.showListAssembly = true;
    }

}
